.App {
  text-align: center;
}

.lotto-checkboxes {
  max-width: 50%;
  margin: 0 auto;
}

.lotto-checkboxes div{
  display: inline-block;
}

/* MODALS */

.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.modal-content {
  width: 90%;
  min-height: 200px;
  background-color: white;
  padding: 25px;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.custom-tooltip {
  background-color: bisque;
  padding: 0px 10px;
  font-size: 15px;
  line-height: 0.5;
  border: 1px solid black;
}

.switch-container {
  /* position: absolute;
  top: 0; right: 0; bottom: 0; left: 0; */
  height: 40px;
  margin: auto;
  text-align: center;
}

.switch input {
  position: absolute;
  opacity: 0;
}

.switch {
  display: inline-block;
  font-size: 20px; /* 1 */
  height: 1em;
  width: 2em;
  background: #BDB9A6;
  border-radius: 1em;
}

.switch div {
  height: 1em;
  width: 1em;
  border-radius: 1em;
  background: #FFF;
  box-shadow: 0 0.1em 0.3em rgba(0,0,0,0.3);
  -webkit-transition: all 300ms;
     -moz-transition: all 300ms;
          transition: all 300ms;
}

.switch input:checked + div {
  -webkit-transform: translate3d(100%, 0, 0);
     -moz-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}